.nav {
    z-index: 1;
    position: fixed;
    top: 50px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 10px 20px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
  }
  
  @media screen and (max-width: 1090px) {
    .nav {
      left: initial;
      top: initial;
      bottom: 0px;
      width: 100%;
    }
  }
  
  .nav a {
    text-decoration: none;
    color: #55b2cc;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
  }
  
  .nav a:not(:last-child) {
    margin-bottom: 20px;
  }


.mainnav a{
    margin-right:10px;
    padding: 16px 24px;
    color: #ffffff;
    background-color: #76d0e9;
    border: 1.5px solid #76d0e9;
    text-decoration: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    position: relative;
    /* display: table; */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    /* padding: 14px 16px; */
    font-size: 18px;
    font-weight: bold;
    text-align: left;
    border-radius: 8px;
}