* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: #eff3f7;
    height: 100%;
  }


.contents-wrapper{
  width:100%;
}

.contents-header-wrapper{
      background-color: transparent;
      -webkit-transition: background-color .5s;
      -o-transition: background-color .5s;
      -moz-transition: background-color .5s;
      transition: background-color .5s;
      position: relative;
      max-width: 1920px;
      margin: 0 auto;
      padding-top: 20px;
      width: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 5rem;
      z-index: 10;
}

.contents-header{
    background-color: transparent;
    -webkit-transition: background-color .5s;
    -o-transition: background-color .5s;
    -moz-transition: background-color .5s;
    transition: background-color .5s;
    position: relative;
    max-width: 1920px;
    margin: 0 auto;
    padding-top: 20px;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 5rem;
    z-index: 10;
}

.contents-header .header{
    position: relative;
    margin: 0 3.5rem;
    height: 100%;
}

.contents-header .header span{
  font-size: 30px;
  font-weight: bold;
  color: #9a9ba4;
}

.contents-header .header a{  
  text-decoration: none;
}


.contents-main-wrapper{
    margin: 0 auto;
}

.contents-main{
    padding: 70px 45px;
    position: relative;
    padding: 50px 5%;
    margin-bottom: 0;
    background: 0 0;
    color: #76d0e9;
}

.contents-main .main{
    position: relative;
    width: 100%;
    padding: 75px 0;
    max-width: 950px;
    margin: 0 auto;
    text-align: center;
    text-decoration: none;
    z-index: 1;
}

.contents-main .main a{
    margin-right:10px;
    padding: 16px 24px;
    color: #ffffff;
    background-color: #76d0e9;
    border: 1.5px solid #76d0e9;
    text-decoration: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 18px;
    font-weight: bold;
    text-align: left;
    border-radius: 8px;
}

